* {
  box-sizing: border-box; }

body {
  font-family: Microsoft JhengHei; }

.wrap {
  width: 100%;
  margin: 0 auto; }

section {
  padding-top: 25px;
  padding-bottom: 25px; }

.course-content {
  padding-left: 30px;
  padding-right: 30px; }
  .course-content .slider {
    margin: 20px auto; }

.slick-slide {
  margin: 12px;
  width: 350px; }
  @media (max-width: 768px) {
    .slick-slide {
      width: 350px; } }
  @media (max-width: 568px) {
    .slick-slide {
      width: 220px;
      margin: 12px 6px; } }
  .slick-slide img {
    width: 100%;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.25); }

.slick-prev:before,
.slick-next:before {
  color: black;
  font-size: 24px; }

.course-search {
  display: inline-block;
  margin-right: 10px; }
  .course-search button {
    padding-left: 30px;
    padding-right: 30px; }
  @media (max-width: 768px) {
    .course-search {
      display: none; } }

.btn-custom {
  background-color: #37761D;
  border: 1px solid #37761D;
  color: #fff;
  padding-left: 10px;
  padding-right: 10px; }

.search-upgrade {
  display: none; }
  @media (max-width: 768px) {
    .search-upgrade {
      display: block;
      float: right;
      padding-top: 10px; } }
  .search-upgrade .modal-body > p {
    display: inline-block; }
  .search-upgrade .modal-body .dropdown {
    display: inline-block;
    margin-bottom: 10px; }
    @media (max-width: 414px) {
      .search-upgrade .modal-body .dropdown {
        display: block; } }

.course-box {
  padding-bottom: 80px; }
  .course-box h2 {
    display: inline;
    font-size: 36px;
    border-bottom: 5px solid #f05f40;
    padding-bottom: 5px; }

.my-course-card {
  margin-top: 20px;
  padding-top: 1px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.25);
  background-color: rgba(255, 255, 255, 0.95); }
  .my-course-card h3 {
    font-size: 36px;
    text-align: center;
    font-weight: bold;
    padding-top: 10px;
    padding-bottom: 15px;
    text-shadow: 2px 2px 3px gray; }
  .my-course-card center a {
    margin-top: 10px;
    margin-bottom: 30px;
    margin-right: 5px; }
  .my-course-card center .btn-pay {
    background-color: #37761D; }
    .my-course-card center .btn-pay:hover {
      background-color: #4a9f27;
      border: 1px solid #4a9f27; }

.card-inform {
  border-radius: 30px;
  min-height: 250px;
  margin-left: 15px;
  margin-right: 15px;
  position: relative; }
  .card-inform .card-status {
    display: inline-block;
    background-color: #4da529;
    padding: 5px 10px;
    border-radius: 10px;
    position: absolute;
    right: 20px;
    bottom: 20px; }
    .card-inform .card-status > a {
      font-size: 24px;
      font-weight: bold;
      color: #fff; }

.first-lesson {
  background-image: url(../img/uian.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top; }
  @media (max-width: 768px) {
    .first-lesson {
      height: 400px; } }

.second-lesson {
  background-image: url(../img/hankcoach.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top; }
  @media (max-width: 768px) {
    .second-lesson {
      height: 400px; } }

.third-lesson {
  background-image: url(../img/wenyen2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top; }
  @media (max-width: 768px) {
    .third-lesson {
      height: 400px; } }

.forth-lesson {
  background-image: url(../img/coachS_Mona.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top; }
  @media (max-width: 768px) {
    .forth-lesson {
      height: 400px; } }

.course-list {
  list-style: none;
  margin-top: 20px;
  padding-right: 30px; }
  .course-list > li > ul > li {
    display: inline-block;
    list-style-type: circle;
    padding: 10px 0;
    border-bottom: 1px solid #D5D8DC;
    font-size: 14px; }

.top-photo {
  background-image: url(http://d3184h1qivcvfe.cloudfront.net/index.jpg);
  background-repeat: no-repeat;
  background-position: left top;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  width: 100%;
  min-height: auto; }
  @media (max-width: 568px) {
    .top-photo {
      background-position: 25% top; } }
  .top-photo h1 {
    font-size: 48px;
    font-weight: bold;
    text-align: center;
    text-shadow: 3px 3px 5px gray;
    color: #fff;
    padding-top: 130px;
    padding-bottom: 130px; }

.service-content .panel-title {
  font-weight: bold; }

.service-content .panel-body ul {
  line-height: 1.5em; }
  .service-content .panel-body ul li {
    margin-bottom: 10px; }

.date-content h2 {
  margin: 0;
  padding-left: 5px;
  padding-bottom: 20px; }

.date-content ul {
  list-style: none;
  margin-top: -10px;
  padding: 0;
  margin-left: 10px;
  padding-bottom: 80px; }
  .date-content ul i {
    display: none; }
    @media (max-width: 768px) {
      .date-content ul i {
        display: inline-block;
        padding-top: 18px;
        padding-right: 15px; } }
  @media (max-width: 768px) {
    .date-content ul span {
      display: none; } }

/* arrow for step process */
.arrow {
  float: left;
  width: 25%;
  height: 50px;
  background-color: rgba(55, 118, 29, 0.3);
  border-radius: 5px 5px 0 0;
  position: relative;
  margin-right: 30px;
  margin-bottom: 20px;
  text-align: center;
  line-height: 45px; }
  .arrow > a {
    display: block;
    text-decoration: none;
    color: #37761D;
    font-size: 15px;
    font-weight: bold; }
  @media (max-width: 568px) {
    .arrow {
      width: 17%; } }

.arrow:after {
  content: '';
  position: absolute;
  right: -22px;
  top: 30%;
  width: 0;
  height: 0;
  border: 10px solid transparent;
  border-left: 12px solid rgba(55, 118, 29, 0.3); }

.arrow-active {
  background-color: rgba(55, 118, 29, 0.7); }
  .arrow-active > a {
    color: #fff; }

.arrow-active:after {
  border-left: 12px solid rgba(55, 118, 29, 0.7); }

.arrow-none:after {
  border: 0; }

.cursor-not-allow {
  cursor: not-allowed; }

.cursor-initial {
  cursor: default; }

.target-select > h3 {
  font-size: 28px;
  font-weight: bold; }
  .target-select > h3 small {
    color: red; }

.target-select .form-qa {
  padding-top: 10px; }
  .target-select .form-qa label {
    font-size: 15px; }

.target-select .target-time {
  display: inline-block;
  padding: 10px 30px;
  border: 1px solid gray;
  border-radius: 5px;
  position: relative; }

.target-select .next-step {
  width: 100%;
  background-color: #37761d;
  border: 1px solid #37761d;
  border-radius: 10px;
  padding: 5px 10px;
  margin-top: 50px;
  color: #fff;
  font-weight: bold; }
  .target-select .next-step:hover {
    background-color: #459725;
    border: 1px solid #459725; }

.ui-widget-header {
  border: 1px solid #37761D;
  background-color: #37761D !important; }
